@media (min-width: 576px) {
  .container {
    max-width: 540px; } }

@media (min-width: 768px) {
  .container {
    max-width: 720px; } }

@media (min-width: 992px) {
  .container {
    max-width: 960px; } }

@media (min-width: 1200px) {
  .container {
    max-width: 1210px; } }

@media (max-width: 1200px) {
  .contacts__whours {
    display: none; }
  .mobile {
    display: block; }
  .menu-toggle {
    display: flex;
    background: #FFCF25;
    margin: 0 0 0 10px; }
  .header {
    .contacts {
      text-align: right; }
    &__button {
      padding-left: 0;
      font-size: 14px;
      line-height: 18px;
      margin-top: -5px;
      &::before {
        content: none; } }
    &__top {
      background: #232429;
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      &-inner {
        border: 0;
        padding: 10px 0; } }
    &__bottom {
      &-inner {
        .header__button {
          display: none; }
        .menu {
          background: #232429;
          position: fixed;
          top: -100%;
          left: 0;
          right: 0;
          z-index: 20;
          opacity: 0;
          @include tr;
          &.active {
            top: 60px;
            opacity: 1; }
          &-items {
            display: block; }
          &__item {
            margin: 0;
            &:first-child {
              a {
                border-top: 1px solid rgba(#FFCF25, .25); } } }
          &__link {
            display: block;
            padding: 10px 15px;
            border-bottom: 1px solid rgba(#FFCF25, .25); } } } }

    .logo {
      &__image {
        width: 200px; } } }
  .header {
    &_fixed {
      display: none !important; } }
  .intro {
    padding: 20px 0 30px; }
  .services {
    padding: 40px 0;
    &__item {
      padding: 25px;
      &:hover {
        .services__item-button {
          bottom: 25px; } }
      &-title {
        font-size: 20px;
        line-height: normal; }
      &-button {
        left: 25px;
        right: 25px; } } }
  .about {
    &__inner {
      padding: 30px; }
    &__left {
      width: 100%;
      margin-bottom: 30px; }
    &__right {
      width: 100%;
      padding-left: 0; } }
  .price {
    padding: 40px 0; }
  .benefits {
    padding: 40px 0; }
  .scheme {
    padding: 0 0 40px; }
  .portfolio {
    padding: 0 0 40px; }
  .content {
    padding-bottom: 40px; }
  .footer {
    .logo {
      width: 200px; }
    &__bottom {
      &-inner {
        padding: 10px 0 15px;
        justify-content: space-between;
        & > * {
          margin: 0; } } } } }

