@media (max-width: 768px) {
  .header {
    &__top {
      &-inner {
        padding: 10px 0; } }
    .social {
      display: none; }
    .contacts {
      margin-left: auto; }
    &_fixed {
      .contacts {
        &__phone {
          font-size: 15px;
          margin-bottom: 0; } } } }
  h1, h2 {
    font-size: 30px !important;
    line-height: normal !important; }
  .form {
    padding: 20px;
    &__inner {
      grid-template-columns: 1fr;
      grid-gap: 20px; }
    &__header {
      font-size: 24px;
      line-height: normal; } }
  .services {
    &-items {
      grid-template-columns: 1fr; } }
  .about__benefit-header {
    font-size: 48px; }
  .scheme {
    &-items {
      &::before {
        content: none; } }
    &__item {
      width: 100%;
      margin-bottom: 30px;
      &::before, &::after {
        content: none; }
      &:last-child {
        margin-bottom: 0; } } }
  .footer {
    .logo {
      width: 100%;
      text-align: center;
      &__image {
        width: 200px; } }
    &__top {
      &-inner {
        justify-content: center;
        padding: 15px 0;
        & > * {
          margin: 10px 0 !important; } } }
    .social {
      width: 100%;
      justify-content: center; } } }

@media (max-width: 576px) {
  .header__button {
    display: none; }
  h1, h2 {
    font-size: 22px !important; }
  .about__benefit-header {
    font-size: 30px; }
  .price {
    &-items {
      grid-template-columns: 1fr; } }
  .benefits {
    &-items {
      grid-template-columns: 1fr; } }
  .form {
    &__header {
      font-size: 20px; }
    &__input {
      height: 40px; }
    &__button {
      height: 40px; } }
  .modal {
    padding: 30px; }
  .header {
    .menu-toggle {
      width: 36px;
      height: 36px; }
    .logo {
      &__image {
        width: 170px; } }
    .contacts {
      &__phone {
        font-size: 0;
        width: 36px;
        height: 36px;
        display: block;
        border-radius: 50%;
        background: #FFCF25 url('../img/phone-black.svg') no-repeat center; }
      &__whours {
        display: none; } }
    &_fixed {
      .social {
        display: none; }
      .header__button {
        display: none; }
      .contacts {
        &__phone {
          font-size: 0;
          width: 32px;
          height: 32px;
          display: block;
          border-radius: 50%;
          background: #FFCF25 url('../img/phone-black.svg') no-repeat center;
          background-size: 16px; } } } }
  .portfolio__map {
    height: 400px; } }

@media (max-width: 400px) {
  .services {
    &-items {
      display: flex;
      overflow: auto;
      padding-bottom: 20px; }
    &__item {
      min-width: 90%; } }
  .price {
    &-items {
      display: flex;
      overflow: auto;
      padding-bottom: 20px; }
    &__item {
      min-width: 90%; } }
  .benefits {
    &-items {
      display: flex;
      overflow: auto;
      padding-bottom: 20px; }
    &__item {
      min-width: 90%; } }
  .scheme {
    &-items {
      display: flex;
      overflow: auto;
      padding-bottom: 20px;
      grid-gap: 20px; }
    &__item {
      min-width: 90%; } } }
